
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
data () {
return {
progressArr: [{
label: '提交',
value: 0
}, {
label: 'DFS/DM',
value: 1
}, {
label: 'PCD',
value: 2
}, {
label: 'PM',
value: 2.1
}, {
label: '区域',
value: 3
}, {
label: '渠道部',
value: 4
}, {
label: '销发部',
value: 5
}],
info: {}
}
},
methods: {
goBack () {
this.$util.getMachine(4)
},
toApply () {
this.$router.push({
path: '/openApply'
})
},
toInfo () {
this.$router.push({
path: '/basicInfo'
})
}
},
created () {
this.$tupHttp(this, 'front/user/openning/info').then(rs => {
let auditStatus = rs.data.model.auditStatus
if (auditStatus === 2 || auditStatus === 8) {
auditStatus = 2.1
} else if (auditStatus === 7) {
auditStatus = 2
} else if (auditStatus === -7) {
auditStatus = -2
} else if (auditStatus === -8) {
auditStatus = -2.1
}
rs.data.model.auditStatus = auditStatus
this.info = rs.data.model
})
},
components: {
XHeader
}
}



